.modal {
  position: absolute;
  display: block;
  width: 100%;
  height: 100vh;
  z-index: 10;

  background-color: rgba(0, 0, 0, 0.5);
}

.modal__container {
  position: relative;
  top: 30px;
  display: flex;
  flex-direction: column;

  width: 300px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 10px;
  padding-left: 10px;
  padding-top: 20px;
  padding-bottom: 20px;

  background-color: $blue;

  z-index: 11;

  @media (min-width: $tablet-width) {
    width: 490px;
    top: 20px;
    padding-top: 33px;
    padding-right: 39px;
    padding-left: 39px;
    padding-bottom: 36px;
  }

  @media (min-width: $desktop-width) {
    top: 136px;
  }
}

.modal__form {
  @media (min-width: $tablet-width) {
    width: 410px;
    overflow: visible;
  }
}

.modal h3 {
  margin: 0;
  margin-bottom: 20px;

  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 28px;
  line-height: 40px;
}

.modal p {
  margin: 0;
  margin-bottom: 30px;

  line-height: 22px;

  @media (min-width: $tablet-width) {
    width: 380px;
  }
}

.modal__close {
  position: relative;
  top: -168px;
  left: 266px;
  width: 17px;
  height: 17px;
  padding: 0;

  background-color: transparent;
  border: none;
  cursor: pointer;

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 1;
  }


  @media (min-width: $tablet-width) {
    top: -149px;
    left: 416px;
  }
}

.modal__close svg {
  transform: rotate(135deg);
  fill: $white;
}

.modal__form input,
.modal__form textarea {
  @media (min-width: $tablet-width) {
    width: 410px;
    margin-bottom: 30px;
  }
}

.modal__form textarea {
  @media (min-width: $tablet-width) {
    height: 104px;
  }
}

.modal__form button {
  @media (min-width: $tablet-width) {
    width: 410px;
    margin-bottom: 34px;
  }
}

.modal__form label {
  @media (min-width: $tablet-width) {
    width: 410px;
  }
}
